import * as types from './actionTypes';
import firebase from 'firebase/app';
import "firebase/firestore";

export const setCurrentUserId = (payload) => ({ type: types.SET_CURRENT_USER_ID, payload });
export const setUserStatus = (payload) => ({ type: types.SET_USER_STATUS, payload });
export const resetUser = () => ({ type: types.RESET_USER });

export const signInAsync = (userName) => async dispatch => {
    const db = firebase.firestore();
    const usersRef = db.collection("users");

    dispatch(setUserStatus("LOGIN_START"));

    usersRef.where("name", "==", userName.toLowerCase()).get()
        .then((res) => {
            if (res.size === 1) {
                res.forEach(item => {
                    dispatch(setUserStatus("LOGIN_SUCCESS"));
                    dispatch(setCurrentUserId(item.id))
                });
            }
        })
        .catch((err) => {
            dispatch(setUserStatus("LOGIN_FAILED"))
        });
}